*
{
    margin:0;
    padding: 0;
}
span
{
    color: rgba(225, 232, 27, 0.529);
}
nav
{
    background-color: rgba(204, 233, 92, 0.319);
    color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}
nav h1
{
    color: rgb(19, 17, 14);
    font-size: 20px;
    font-weight: 600;
    margin-left: 2px;
}
nav ul
{
    display: flex;
    justify-content: space-between;
    width: 40%;
    align-items: center;
}
nav ul a
{
    font-size: 20px;
    font-weight: 500;
    color: rgb(217, 227, 71);
    text-decoration: none;
}
nav ul a:hover
{
    color:white;
}