*
{
    margin: 0;
    padding: 0;
}
.user
{
    background-image: url("https://t3.ftcdn.net/jpg/03/24/69/66/360_F_324696653_YMo1QD9oKhmgrQPDxEV8LM7xHzxn74zW.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 10px 8px 15px -2px rgba(135, 135, 135, 0.37);
    background-color: transparent;
    margin-bottom: 30px;
    margin-left: 20px;
    width: 500px;
    padding: 20px;
}
.user h2
{
    font-size: 15px;
    font-weight: 700;
    color: rgb(235, 228, 228);
}
.user span
{
    font-size: 15px;
    font-weight: 500;
    color: rgb(190, 238, 46);
}
.user__delete
{
    background-color: brown;
    color: white;
    padding: 10px;
    border-radius: 5px;
}
.user__button
{
    margin-top: 20px;
}