*
{
    margin: 0;
    padding: 0;
}
.users__title
{
    margin-top: 10px;
    color: rgb(221, 223, 201);
    font-size: 25px;
    font-weight: 700;
    margin-left: 10px;
}
.users__empty
{
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: self-start;
    width: 100%;
    height: 100%;
    color: rgb(216, 210, 28);
    margin-left: 10px;
}
.users h3
{
    background-color: rgb(218, 129, 129);
    padding: 10px;
    margin-top: 20px;
    margin-left: 200px;
    text-decoration: none;
    color: white;
    border-radius: 8px;
}