*
{
    margin: 0;
    padding: 0;
}
.abt
{
    background-image: url("https://wallpapergod.com/images/hd/school-1920X1080-wallpaper-9dyxtlau1wwoa1ma.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh; 
    width: 100%;
    margin-bottom: 0px;
}
.about
{
    display: block;
    text-align: center;
    color: rgb(191, 238, 238);
}
.vision
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-left: 5px;
    font-size: 30px;
    font-weight: 700;
    font-style: italic;
    color: rgb(218, 235, 27);
    margin-top:0px;
}
.vision__detail
{
    font-size: 15px;
    margin-top: 15px;
    font-weight: 600;
    margin-bottom: 20px;
    color: rgb(21, 21, 21);
    font-style: italic;
}
.mission
{
    margin-left: 7px;
    margin-top: 15px;
    font-size: 30px;
    font-weight: 700;
    font-style: italic;
    color: rgb(218, 235, 27);
}
.mission__detail
{
    font-size: 15px;
    margin-top: 15px;
    font-weight: 600;
    margin-bottom: 20px;
    color: rgb(21, 21, 21);
    font-style: italic;
}
.characters
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    font-size: 30px;
    font-weight: 700;
    font-style: italic;
    color: rgb(218, 235, 27);  
}
.detail
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    margin-top: 15px;
    font-weight: 600;
    margin-left: 15px;
    margin-bottom: 20px;
    color: rgb(21, 21, 21);
    font-style: italic;
    list-style-type: none;
}
.duty
{
    font-size: 15px;
    margin-top: 15px;
    font-weight: 600;
    margin-left: 15px;
    margin-bottom: 20px;
    color: rgb(21, 21, 21);
    font-style: italic;
}