*
{
    margin: 0;
    padding: 0;
}
body
{
    background-image: url("https://wallpapers.com/images/hd/back-to-school-background-tgvotvt5odvma1wj.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}
.home
{
    margin: 0 auto;
    margin-top: 90px;
    width: 100%;
    height: 100%;
}
.home__container
{
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home__formContainer
{
    box-shadow: 1px 4px 12px -2px rgba(0, 0, 0, 4.3);
    background-color: transparent;
    padding: 30px;
    border-radius: 10px;
    width: 500px;
    margin-left: 720px;
}
.home__title
{
    color: rgb(211, 225, 83);
    margin-left: 120px;
}
.home__formContainer input
{
    border: 1px solid white;
    outline: none;
    padding: 15px 20px;
    width: 80%;
    border-radius: 8px;
    margin: 10px 25px;
    background: transparent;
    color: aliceblue;
    font-size: 14px;
}
.home__formContainer input::placeholder
{
    color: rgb(232, 232, 240);
}
.home__formContainer button
{
    background-color: rgb(91, 94, 94);
    border: 1px solid white;
    outline: none;
    font-size: 18px;
    color: white;
    padding: 10px;
    width: 200px;
    margin-left: 150px;
    margin-top: 25px;
    border-radius: 12px;
    cursor: pointer;
}